.login {
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: black;
    padding-left: 30px;
}

.login>img {
    width: 100%;
    width: 700px;
    margin-right: 40px;
    margin-top: 100px;
}

.login>a {
    padding: 20px;
    background-color: #1db954;
    font-weight: 800;
    border-radius: 99px;
    color: white;
    text-decoration: none;
    font-size: larger;
}